}

/* Blockquote/Callout Styling */

blockquote {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-left-width: 4px;
    --tw-border-opacity: 1;
    border-color: rgb(216 71 39 / var(--tw-border-opacity, 1));
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
}

blockquote:is(.dark *) {
