}

blockquote p {
    font-weight: 400;
    --tw-text-opacity: 1;
    color: rgb(107 114 128 / var(--tw-text-opacity, 1));
}

blockquote p:is(.dark *) {
    --tw-text-opacity: 1;
    color: rgb(156 163 175 / var(--tw-text-opacity, 1));
}

blockquote strong,
blockquote b {
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgb(41 44 47 / var(--tw-text-opacity, 1));
    color: var(--color-text-body);
}

blockquote strong:is(.dark *),
blockquote b:is(.dark *) {
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity, 1));
}

