.prose blockquote {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-left-width: 4px;
    --tw-border-opacity: 1;
    border-color: rgb(216 71 39 / var(--tw-border-opacity, 1));
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
}

.prose blockquote:is(.dark *) {
    --tw-border-opacity: 1;
    border-color: rgb(249 125 110 / var(--tw-border-opacity, 1));
}

.prose blockquote p {
    font-weight: 400 !important;
    --tw-text-opacity: 1 !important;
    color: rgb(107 114 128 / var(--tw-text-opacity, 1)) !important;
}

.prose blockquote p:is(.dark *) {
    --tw-text-opacity: 1 !important;
    color: rgb(156 163 175 / var(--tw-text-opacity, 1)) !important;
}

.prose blockquote strong,
.prose blockquote b {
    font-weight: 700 !important;
    --tw-text-opacity: 1 !important;
    color: rgb(41 44 47 / var(--tw-text-opacity, 1)) !important;
    color: var(--color-text-body) !important;
}

