    color: rgb(255 255 255 / var(--tw-text-opacity, 1)) !important;
}

/* List styling */

ol,
ul {
    margin-bottom: 1rem;
    padding-left: 1.5rem;
}

/* Sidebar list reset */

#sideNav ul,
#sideNav ol,
.sidebar-links ul,
.categories-toggle ul {
    margin-bottom: 0px;
    list-style-type: none;
    padding-left: 0px;
}

#sideNav li,
.sidebar-links li,
.categories-toggle li {
    margin-bottom: 0px;
}

ol li,
ul li {
    margin-bottom: 0.5rem;
}

ol li::marker {
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgb(216 71 39 / var(--tw-text-opacity, 1));
    color: --orange-valencia;
}

ul li::marker {
    --tw-text-opacity: 1;
    color: rgb(216 71 39 / var(--tw-text-opacity, 1));
    color: --orange-valencia;
}

/* List styling for dark mode */

.dark ol,
.dark ul {
    --tw-text-opacity: 1;
    color: rgb(226 226 226 / var(--tw-text-opacity, 1));
}

.dark ol li::marker,
.dark ul li::marker {
    --tw-text-opacity: 1;
    color: rgb(249 125 110 / var(--tw-text-opacity, 1));
}

.dark .prose ol li::marker,
.dark .prose ul li::marker {
    --tw-text-opacity: 1;
    color: rgb(249 125 110 / var(--tw-text-opacity, 1));
}

.dark .prose ol,
.dark .prose ul {
    --tw-text-opacity: 1;
    color: rgb(226 226 226 / var(--tw-text-opacity, 1));
}

/* Post Meta */

header .meta {
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-size: 0.75rem;
    line-height: 1rem;
    --tw-text-opacity: 1;
    color: rgb(107 114 128 / var(--tw-text-opacity, 1));
}

/* Read More Section */

.gh-readmore {
    width: 100%;
    padding-bottom: 1rem;
}

.gh-readmore-inner {
    display: flex;
    justify-content: space-between;
    font-family: var(--gh-font-heading), sans-serif;
    font-size: 1rem;
    line-height: 1.5rem;
    font-family: var(--gh-font-heading), sans-serif, monospace;
}

.gh-readmore-next,
.gh-readmore-prev {
    transition-property:
        color, background-color, border-color, text-decoration-color, fill,
        stroke, opacity, box-shadow, transform, filter, backdrop-filter;
    transition-duration: 300ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.gh-readmore-next:hover,
.gh-readmore-prev:hover {
    --tw-text-opacity: 1;
    color: rgb(216 71 39 / var(--tw-text-opacity, 1));
    color: --orange-valencia;
}

@keyframes flicker {
    0% {
        opacity: 1;
        transform: scale(1);
        stroke: --orange-valencia;
        /* Main color */
    }

    50% {
        opacity: 0.5;
        transform: scale(1.1);
        stroke: #f75946;
        /* Lighter shade */
    }

    100% {
        opacity: 1;
        transform: scale(1);
        stroke: --orange-valencia-hover;
        /* Darker shade */
    }
}

.flame-icon svg path {
    animation: flicker 1s infinite alternate;
}

@keyframes rotate-hashtag {
    0%,
    100% {
        transform: rotate(0deg);
    }

    50% {
        transform: rotate(20deg);
    }
}

.hashtag {
    display: inline-block;
    animation: rotate-hashtag 1s infinite ease-in-out;
}

.post-card .flex-shrink-0 img {
    height: 8rem;
    /* Fixed height */
    -o-object-fit: cover;
    object-fit: cover;
    /* Cover effect */
    width: 8rem;
    /* Fixed width */
}

.post-card-primary-tag {
    transition: color 0.3s ease;
}

.post-card-primary-tag:hover {
    color: --orange-valencia;
    /* Orange Valencia */
}

.hover-orangeValencia {
    transition: color 0.3s ease-in-out;
}

.hover-orangeValencia:hover {
    color: --orange-valencia;
    /* Orange Valencia */
}

.prose {
    color: var(--color-text-body);
    /* anthracite color */
}

.dark .prose strong,
.dark .prose b,
