/* Subscriber-Only Section */

.gh-post-upgrade-cta {
    border-radius: 0.5rem;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity, 1));
    padding: 1.5rem;
    --tw-text-opacity: 1;
    color: rgb(41 44 47 / var(--tw-text-opacity, 1));
}

.gh-post-upgrade-cta-content {
    border-radius: 0.5rem !important;
    --tw-bg-opacity: 1 !important;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity, 1)) !important;
    padding: 2rem !important;
    text-align: center !important;
    --tw-text-opacity: 1 !important;
    color: rgb(41 44 47 / var(--tw-text-opacity, 1)) !important;
}

.gh-post-upgrade-cta h2 {
    margin-bottom: 1rem;
    font-family: var(--gh-font-heading), sans-serif;
    font-size: 1.5rem;
    line-height: 2rem;
    --tw-text-opacity: 1;
    color: rgb(216 71 39 / var(--tw-text-opacity, 1));
    color: --orange-valencia;
    font-family: var(--gh-font-heading), sans-serif, monospace;
}

.gh-btn.gh-portal-close {
    border-radius: 9999px;
    --tw-bg-opacity: 1;
    background-color: rgb(216 71 39 / var(--tw-bg-opacity, 1));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity, 1));
    transition-property:
        color, background-color, border-color, text-decoration-color, fill,
        stroke, opacity, box-shadow, transform, filter, backdrop-filter;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 300ms;
    background-color: --orange-valencia;
}

.gh-btn.gh-portal-close:hover {
    --tw-bg-opacity: 1;
    background-color: rgb(160 54 29 / var(--tw-bg-opacity, 1));
}

.gh-post-upgrade-cta-content p {
    margin-top: 1rem;
}

.gh-post-upgrade-cta-content p a {
    --tw-text-opacity: 1;
    color: rgb(216 71 39 / var(--tw-text-opacity, 1));
    text-decoration-line: underline;
    transition-property:
        color, background-color, border-color, text-decoration-color, fill,
        stroke, opacity, box-shadow, transform, filter, backdrop-filter;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 300ms;
    color: --orange-valencia;
}

.gh-post-upgrade-cta-content p a:hover {
    --tw-text-opacity: 1;
    color: rgb(160 54 29 / var(--tw-text-opacity, 1));
}

/* Center image captions and adjust spacing */

figure {
    margin: 1.5em auto;
    /* Auto left/right margins for horizontal centering */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.kg-card-hascaption figcaption {
    text-align: center;
    margin-top: 0.5em;
    font-size: 0.75em;
    color: #666;
}

.dark figcaption {
