@media (min-width: 640px) {
    .sm\:mb-0 {
        margin-bottom: 0px;
    }

    .sm\:mr-4 {
        margin-right: 1rem;
    }

    .sm\:w-24 {
        width: 6rem;
    }

    .sm\:grid-cols-2 {
        grid-template-columns: repeat(2, minmax(0, 1fr));
    }

    .sm\:flex-row {
        flex-direction: row;
    }

    .sm\:justify-start {
        justify-content: flex-start;
    }

    .sm\:text-wrap {
        text-wrap: wrap;
    }

    .sm\:text-left {
        text-align: left;
    }

    .sm\:text-4xl {
        font-size: 2.25rem;
        line-height: 2.5rem;
    }

    .sm\:text-5xl {
        font-size: 3rem;
        line-height: 1;
    }

    .sm\:leading-4 {
        line-height: 1rem;
    }

    .sm\:leading-relaxed {
        line-height: 1.625;
    }
}

@media (min-width: 768px) {
    .md\:hidden {
        display: none;
    }

    .md\:h-64 {
        height: 16rem;
    }

    .md\:basis-1\/3 {
        flex-basis: 33.333333%;
    }

    .md\:grid-cols-2 {
        grid-template-columns: repeat(2, minmax(0, 1fr));
    }
}

@media (min-width: 1024px) {
    .lg\:fixed {
        position: fixed;
    }

    .lg\:inset-auto {
        inset: auto;
    }

    .lg\:ml-72 {
        margin-left: 18rem;
    }

    .lg\:hidden {
        display: none;
    }

    .lg\:h-screen {
        height: 100vh;
    }

    .lg\:w-72 {
        width: 18rem;
    }

    .lg\:translate-x-0 {
        --tw-translate-x: 0px;
        transform: translate(var(--tw-translate-x), var(--tw-translate-y))
            rotate(var(--tw-rotate)) skewX(var(--tw-skew-x))
            skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x))
            scaleY(var(--tw-scale-y));
    }

    .lg\:grid-cols-3 {
        grid-template-columns: repeat(3, minmax(0, 1fr));
    }

    .lg\:flex-row {
        flex-direction: row;
    }

    .lg\:flex-col {
        flex-direction: column;
    }

    .lg\:pt-8 {
        padding-top: 2rem;
    }

    .lg\:text-5xl {
        font-size: 3rem;
        line-height: 1;
    }

    .lg\:text-6xl {
        font-size: 3.75rem;
        line-height: 1;
    }

    .lg\:shadow-none {
        --tw-shadow: 0 0 #0000;
        --tw-shadow-colored: 0 0 #0000;
        box-shadow:
            var(--tw-ring-offset-shadow, 0 0 #0000),
            var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
    }
}
